@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@400;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.containerSearch{
    width: 100vw;
    height: 100vh;
    font-size: 100%;
    overflow-x: hidden;
}

.imgLogo{
    display: flex;
    flex-shrink: 1;
    width: 30em;
}

.search{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
input{
    position: relative;
    height:4em;
    width: 50vw;
    max-width: 700px;
    border-radius: 9999px;
    margin-top: 2.5em;
    z-index: 0;
    padding-left: 25px;
    border: 2px #E5E5E5 solid
}
input:focus-visible{
    -webkit-appearance: none;
            appearance: none;
    outline: none;
}
.containerWord{
    display: flex;
}

.butaoSearch{
    position: relative;
    width: 8em;
    height: 5em;
    border-radius: 100px;
    background-color: #E5E5E5;
    margin-top: 2em;
    border:none;
    z-index: 1;
    right: 3.2em;
}

.svgSearch{
    width: 2.5em;
    height: 2.5em;
    max-width: 40px;
    max-height: 40px;
}





header{
    display: flex;
    padding-top: 2em;
    padding-left: 10em;
}
.dream2{
    display: flex;
    width: 100vw;
}
.margim{
    padding-left: 2.3em;
}
span{
    color:#E8833A;
    font-weight: 800;
}

.titleRep{
    padding-left: 0;
    margin: 0;

}

.center{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-left:10em;
    margin-top: 4em;
    margin-right: 10em;
}

.Repo{
    display: flex;
    flex-direction: column;
    padding: 0;
    width: 50em;
    margin-top:2em;
    
}
.infGeral{
    display: flex;
    flex-direction: column;
    width: 50em;
    
}
.tudoCentro{
    display: flex;
    flex-direction: column;
    align-content: center;
    overflow-x: hidden;
    font-size: 100%;
    height: 100vh;
    background-color: #f9f9f9;
}
.listRepo{
    display: flex;
    justify-content: space-between;
    transition: box-shadow 1s ease;
    border-radius: 10px;
    border-bottom: 2px solid #dbc0ac;
    cursor: default;
    margin-bottom: 3em;
}

.listRepo:hover{
    box-shadow: 0 0 10px #f9f9f9;
}

.dateRepo{
    margin: 0;
    margin-top: 0.5em;
    margin-right: 2em;
}
.infRepo{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}
.titleRepo{
    margin: 0;
    align-self: flex-start;
    margin-bottom: 1em;
    font-size: 2em;
    width: 15em;
    text-transform: capitalize;
}

.descriptionRepo{
    width: 30em;
}

.repoButton{
    height: 3em;
    width: 5em;
    border-radius: 15px;
    border: none;
    background-color: #E8833A;
    transition: background-color 2s ease;
    cursor: pointer;
}

.repoButton:hover{
    background-color: black;
}

.textGit{
    margin: 0;
    color: #f9f9f9;
    font-weight: 800;
}
a{
    text-decoration: none;
}
@media (max-width: 1100px){
    .tudoCentro{
        font-size: 80%;
    }
}

@media (max-width: 800px){
    .tudoCentro{
        font-size: 60%;
    }
}

@media (max-width: 685px){
    .tudoCentro{
        font-size: 40%;
    }
}
.search2{
    display: flex;
    justify-content: space-around;
}

.imgLogo2{
    width: 8em;
}

#searchRoute{
    display: flex;
    flex-grow: 1;
    width: 50vw;
    max-width: 700px;
    height: 3.5em;
}
h1{
    text-transform: capitalize;
}
.butaoSearch2 {
    position: relative;
    width: 6em;
    height: 4em;
    border-radius: 100px;
    background-color: #E5E5E5;
    margin-top: 2.2em;
    border: none;
    z-index: 1;
    right: 5em;
}
form{
    display: flex;
}
.svgSearch2{
    width: 2em;
    max-width: 30px;
}

.dream{
    display: flex;
}

.containerDados{
    display: flex;
    height:100%;
    flex-direction: column;
    align-content: flex-start;
    align-items: center;
    margin-top: 2em;
}

.containerName{
    display: flex;
    width: 50em;
    background-color: #000000;
    color: #F9F9F9;
    border-top-right-radius:10px;
    border-top-left-radius: 10px;
}

.containerOthers{
    display: flex;
    flex-direction: column;
    width: 50em;
    height: auto;
    background-color: #F9F9F9;
    border-bottom-right-radius:10px;
    border-bottom-left-radius: 10px;
}

.imgUser{
    margin-top: 3em;
    display: flex;
    border-radius: 9999px;
    width: 15em;
    height: 15em;
    align-self: center;
}

.containerHex{
    display: flex;
    flex-direction: row;
    align-self: center;
}

ul{
    list-style-type: none;
}

.dates{
    display: flex;
    flex-direction: column;
    background-image:url("/static/media/Polygon BG.95a1c2ea.svg");
    background-repeat: no-repeat;
    background-size: contain;
    width: 7.1875em;
    height: 8em;
    align-items: center;
    margin-right: 2em;
    align-content: center;
    transition: color 0.5s ease;;
    cursor: pointer;
}

.dates:hover{
    color:#E8833A;
}

h1{
    padding-left: 3em;
}

h2{
    font-family: 'Manrope', sans-serif;
    font-weight: 800;
    font-size: 2em;
    margin-bottom: 0;
}

p{
    font-family: 'Manrope', sans-serif;
    margin-top: 0;
}

.buttonGithub{
    display: flex;
    width: 8em;
    height: 3em;
    align-self: center;
    border-radius: 9999px;
    border: none;
    background: #E8833A;
    align-content: center;
    align-items: center;
    transition: background-color 0.5s ease;;
    cursor: pointer;
    margin-bottom: 2em;
}
.buttonGithub:hover{
    background-color:#000000;
}
.githubText{
    width: 8em;
    margin: 0;
    font-weight: 800;
    color: #F9F9F9;
    font-size: 0.9em;
}

@media (max-width: 1100px){
    .containerSearch{
        font-size: 80%;
    }
}

@media (max-width: 800px){
    .containerSearch{
        font-size: 60%;
    }
}

@media (max-width: 685px){
    .containerSearch{
        font-size: 40%;
    }
    .containerHex{
        padding-left: 2em;
    }
    .butaoSearch{
        width: 6em;
    }
}
